import { createContext, useReducer } from 'react';

export const Store = createContext();

const userInfoFromStorage = localStorage.getItem('userInfo');
let userInfo = null;
if (userInfoFromStorage) {
  try {
    userInfo = JSON.parse(userInfoFromStorage);
  } catch (error) {
    console.error('Error parsing userInfo from localStorage:', error);
  }
}

const shippingAddressFromStorage = localStorage.getItem('shippingAddress');
let shippingAddress = {};
if (shippingAddressFromStorage) {
  try {
    shippingAddress = JSON.parse(shippingAddressFromStorage);
  } catch (error) {
    console.error('Error parsing shippingAddress from localStorage:', error);
  }
}

const paymentMethodFromStorage = localStorage.getItem('paymentMethod');
let paymentMethod = '';
if (paymentMethodFromStorage) {
  paymentMethod = paymentMethodFromStorage;
}

const cartItemsFromStorage = localStorage.getItem('cartItems');
let cartItems = [];
if (cartItemsFromStorage) {
  try {
    cartItems = JSON.parse(cartItemsFromStorage);
  } catch (error) {
    console.error('Error parsing cartItems from localStorage:', error);
  }
}

const initialState = {
  userInfo,
  cart: {
    shippingAddress,
    paymentMethod,
    cartItems,
  },
};

function reducer(state, action) {
  switch (action.type) {
    case 'CART_ADD_ITEM':
      // add to cart
      const newItem = action.payload;
      const existItem = state.cart.cartItems.find(
        (item) => item._id === newItem._id
      );
      const cartItems = existItem
        ? state.cart.cartItems.map((item) =>
            item._id === existItem._id ? newItem : item
          )
        : [...state.cart.cartItems, newItem];
      localStorage.setItem('cartItems', JSON.stringify(cartItems));
      return { ...state, cart: { ...state.cart, cartItems } };
    case 'CART_REMOVE_ITEM': {
      const cartItems = state.cart.cartItems.filter(
        (item) => item._id !== action.payload._id
      );
      localStorage.setItem('cartItems', JSON.stringify(cartItems));
      return { ...state, cart: { ...state.cart, cartItems } };
    }
    case 'CART_CLEAR':
      return { ...state, cart: { ...state.cart, cartItems: [] } };
    case 'USER_SIGNIN':
      return { ...state, userInfo: action.payload };
    case 'USER_SIGNOUT':
      return {
        ...state,
        userInfo: null,
        cart: {
          cartItems: [],
          shippingAddress: {},
          paymentMethod: '',
        },
      };
    case 'SAVE_SHIPPING_ADDRESS':
      return {
        ...state,
        cart: {
          ...state.cart,
          shippingAddress: action.payload,
        },
      };
    case 'SAVE_PAYMENT_METHOD':
      return {
        ...state,
        cart: { ...state.cart, paymentMethod: action.payload },
      };
    default:
      return state;
  }
}

export function StoreProvider(props) {
  const [state, dispatch] = useReducer(reducer, initialState);
  const value = { state, dispatch };
  return <Store.Provider value={value}>{props.children}</Store.Provider>;
}
